import React, { useEffect, useState } from 'react'
import PlaceAttributes from "../assets/PlaceAttributes.json";
import Drought_Images from "../assets/Drought_Images.json"
import { useSelectedFeatureContext } from '../contexts/SelectedFeatureContext';
import { useLoaderContext } from '../contexts/LoaderContext';
import { useTranslation } from 'react-i18next';

const DroughtPage = () => {
    const { handleDistrictSelect, selectedDistrict } = useSelectedFeatureContext();
    const [selectedImage, setSelectedImage] = useState(null);
    const { t } = useTranslation();




    console.log(selectedDistrict)
    useEffect(() => {
        if (selectedDistrict && selectedDistrict !== '') {
            let item = Drought_Images.find((item) => item.DISTRICT === selectedDistrict);
            setSelectedImage(item);

        }
    }, [selectedDistrict]);








    return (
        <div className='dasboard_page_container'>

            <div className='main_dashboard'>
                <div className='left_panel'>

                    <div className="left_panel_container">
                        <div className=' border-secondary p-2 mb-2'>

                            <label className=''>{t('data.select_district')}</label>
                            <select className="form-select mb-3"
                                value={selectedDistrict ? selectedDistrict : ''}
                                onChange={handleDistrictSelect}>
                                <option value="">{t('data.select_placeholder')}</option>
                                {[...new Set(PlaceAttributes.map((item) => item.DISTRICT))].map((item, index) =>
                                    <option key={index} value={item}>{t(`location.${item}`)}</option>
                                )}
                            </select>


                        </div>
                    </div>


                </div>

                <div className='right_panel'>
                    <div className='right_panel_container'>
                        {selectedImage ? (
                            <img src={selectedImage.url} alt='Drought Conditions' />
                        ) : (
                            <p>
                                {t('data.select_location_placeholder')}
                            </p>
                        )}
                    </div>




                </div>

            </div>
        </div>
    )
}

export default DroughtPage